body {
  font-family: Arial;
}

.breed-select {
  text-align: center;
  color: charcoal;
  border-radius: 5px;
  font-family: Arial;
  font-size: 30px;
}

.text {
  max-width: 500px;
}

.cat-info {
  min-height: 70vh;
  justify-content: center;
  align-items: center;
  gap: 50px;
  display: flex;
  position: relative;
}

.text-box {
  flex-flow: column;
  justify-content: flex-end;
  display: flex;
}

.cat-info__loader {
  flex-direction: column;
  align-items: center;
  font-weight: 600;
  display: flex;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%);
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #000;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  background: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, 50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}

